/* Google Font Link */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&amp;display=swap');
  
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
  /* background: #0059FF; */
}
/* body {
  background-image: none !important;
} */

.container {
  background: #EDF0F6;
  background: linear-gradient(135deg, #00feba, #5b548a);
  /* background: linear-gradient(354deg, #137DF5 0%, #14B1F8 100%); */
  /* padding: 30px 120px; */
  width: 320px;
  height: 600px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  border-radius: 10px;
}
 
.container_date
{
  color: white;
  margin-top:76px;
  opacity: 47%;
  font-size: 22px;
  font-weight: 400;
}
 
.container_city
{
  margin-top: 1rem;
  font-size: 37px;
  font-weight: 700;
  color: white;
 
}
 
.container_degree{
  font-size: 50px;
  font-weight: 800;
  color: white;
  position: relative;
  top: -20px;
}
 
.container_img
{
  position: relative;
  top: -29px;
}
 
.degree_icon
{
  font-size: 20px;
}
 
.country_per
{
  position: relative;
  top: -15px;
  color: white;
  font-size: 18px;
}
 
.input {
  border: none;
  padding: 1rem;
  border-radius: 0px 0px 0px 15px;
  background: #e8e8e8;
  /* box-shadow: 20px 20px 60px #c5c5c5, -20px -20px 60px #ffffff; */
  transition: 0.3s;
  position: relative;
  top: -2px;
 }
  
 .input:focus {
  outline-color: #e8e8e8;
  background: #e8e8e8;
  box-shadow: inset 20px 20px 60px #e8e8e8,
     inset -20px -20px 60px #ffffff;
  transition: 0.3s;
 }
 
 /* button */
 
 button 
  {
    color: #090909;
    padding: 0.7em 0.7em;
    font-size: 18px;
    /* border-radius: 0.5em; */
    /* background: #e8e8e8; */
    cursor: pointer;
    border: 1px solid #e8e8e8;
    transition: all 0.3s;
}
 
button:active {
  color: #666;
  box-shadow: inset 4px 4px 12px #c5c5c5, inset -4px -4px 12px #ffffff;
}
button {
  padding: 10px 20px;
  margin-top: 10px;
  background-color: #1e90ff;
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: bold;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

button:hover {
  background-color: #0077cc;
  transform: scale(1.05);
}

.input {
  padding: 10px;
  font-size: 1rem;
  width: 70%;
  border-radius: 8px;
  border: 1px solid #ccc;
  margin-right: 10px;
}

.form {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.location-btn {
  margin-top: 20px;
  background-color: #32cd32;
}

.location-btn:hover {
  background-color: #28a428;
}

.form
{
  margin-top: 3rem;
}


